<script>
import {postEstimateData, getEstimateDataOne, getEstimateDetailsDataAll} from 'api/estimates'
import {getProductDataAll} from 'api/products'
import EstimateDetailsForm from 'components/content/project/EstimateDetailsForm'
import EstimateDetailsTable from 'components/content/project/EstimateDetailsTable'
// import {getCustomerDataOne} from 'api/customers'

export default {
  name: 'EstimateForm',
  components: {
    EstimateDetailsForm,
    EstimateDetailsTable
  },
  props: {
    cEstimateForm: {
      type: Object,//指定传入的类型,type 也可以是一个自定义构造器函数，使用 instanceof 检测。
      default: function () { //这样可以指定默认的值
        return {
          ProjectCode: '',
          ProjectName: '',
          CustomerName: '',
          CustomerCode: '',
          EstimateCode: '',
          uuid: ''
        }
      }
    }
  },
  data() {
    return {
      activeNames: ['1', '2'],
      ruleForm: {
        restaurants: [],
        EstimateCode: this.cEstimateForm.EstimateCode,
        ProjectCode: this.cEstimateForm.ProjectCode,
        ProjectName: this.cEstimateForm.ProjectName,
        CustomerName: this.cEstimateForm.CustomerName,
        EstimateDate: [],
        Work1: '',
        Work2: '',
        Work3: '',
        Deliverables1: '',
        Deliverables2: '',
        Deliverables3: '',
        Media1: '',
        Media2: '',
        Media3: '',
        Quantity1: '',
        Quantity2: '',
        Quantity3: '',
        DeliveryDate1: '',
        DeliveryDate2: '',
        DeliveryDate3: '',
        WorkSpace: '',
        SubTotal: '',
        Tax: '',
        Total: '',
        Supplement: '',
        Remarks: '',
        PaymentConditions: '',
        Other: ''
      },
      ProductsDataAll: [],
      initialDetailsTableData: [],
      runningDetailsTableData: [],
      rules: {
        Work1: [
          {required: true, message: '作業内容を入力してください', trigger: 'change'}
        ],
        EstimateDate: [
          {required: true, message: '見積日と見積有効日を入力してください', trigger: 'change'}
        ],
        Deliverables1: [
          {required: true, message: '成果物を入力してください', trigger: 'blur'}
        ],
        Media1: [
          {required: true, message: '納品媒体を入力してください', trigger: 'blur'}
        ],
        Quantity1: [
          {required: true, message: '部数を入力してください', trigger: 'blur'}
        ],
        DeliveryDate1: [
          {required: true, message: '納品予定日を入力してください', trigger: 'blur'}
        ],
        WorkSpace: [
          {required: true, message: '作業場所を入力してください', trigger: 'blur'}
        ],
        PaymentConditions: [
          {required: true, message: '支払条件を入力してください', trigger: 'blur'}
        ]
      }
    }
  },
  computed: {
    sumSubTotal() {
      let sumsubtotal = 0
      for (let initialData of this.initialDetailsTableData) {
        sumsubtotal += parseFloat(initialData.SubTotal)
      }
      for (let runningData of this.runningDetailsTableData) {
        sumsubtotal += parseFloat(runningData.SubTotal)
      }
      return sumsubtotal.toString().replace(/(\d)(?=(\d{3})+$)/g, '$1,')
    },
    sumTax() {
      let sumtax = 0
      for (let initialData of this.initialDetailsTableData) {
        sumtax += parseFloat(initialData.SubTotal)
      }
      for (let runningData of this.runningDetailsTableData) {
        sumtax += parseFloat(runningData.SubTotal)
      }
      sumtax = parseFloat(sumtax) * 0.1
      return sumtax.toString().replace(/(\d)(?=(\d{3})+$)/g, '$1,')
    },
    sumTotal() {
      let sumtotal = 0
      for (let initialData of this.initialDetailsTableData) {
        sumtotal += parseFloat(initialData.SubTotal)
      }
      for (let runningData of this.runningDetailsTableData) {
        sumtotal += parseFloat(runningData.SubTotal)
      }
      sumtotal += sumtotal * 0.1
      return sumtotal.toString().replace(/(\d)(?=(\d{3})+$)/g, '$1,')
    }
  },
  created: function () {
    this.getEstimateDetailsDataAll()
    if (this.cEstimateForm.EstimateCode) {
      this.getEstimateDataOne()
    }
    this.getProductDataAll()
  },
  methods: {
    postEstimateData() {
      let values = {}
      values['EstimateCode'] = this.ruleForm.EstimateCode
      values['ProjectCode'] = this.ruleForm.ProjectCode
      values['ProjectName'] = this.ruleForm.ProjectName
      values['CustomerName'] = this.ruleForm.CustomerName
      values['EstimateDate'] = this.ruleForm.EstimateDate
      values['Work1'] = this.ruleForm.Work1
      values['Work2'] = this.ruleForm.Work2
      values['Work3'] = this.ruleForm.Work3
      values['Deliverables1'] = this.ruleForm.Deliverables1
      values['Deliverables2'] = this.ruleForm.Deliverables2
      values['Deliverables3'] = this.ruleForm.Deliverables3
      values['Media1'] = this.ruleForm.Media1
      values['Media2'] = this.ruleForm.Media2
      values['Media3'] = this.ruleForm.Media3
      values['Quantity1'] = this.ruleForm.Quantity1
      values['Quantity2'] = this.ruleForm.Quantity2
      values['Quantity3'] = this.ruleForm.Quantity3
      values['DeliveryDate1'] = this.ruleForm.DeliveryDate1
      values['DeliveryDate2'] = this.ruleForm.DeliveryDate2
      values['DeliveryDate3'] = this.ruleForm.DeliveryDate3
      values['WorkSpace'] = this.ruleForm.WorkSpace
      values['SubTotal'] = this.sumSubTotal
      values['Tax'] = this.sumTax
      values['Total'] = this.sumTotal
      values['Supplement'] = this.ruleForm.Supplement
      values['Remarks'] = this.ruleForm.Remarks
      values['PaymentConditions'] = this.ruleForm.PaymentConditions
      values['Other'] = this.ruleForm.Other
      values['DateTime'] = this.newDateTime
      postEstimateData(values).then(res => {
        if (res.status == 200) {
          this.$emit('submit')
          this.$message({
            showClose: true,
            message: '見積書データ登録・更新成功しました。',
            type: 'success'
          });
        } else {
          this.$emit('cancel')
          this.$message({
            showClose: true,
            message: '見積書データ登録・更新失敗しました。',
            type: 'error'
          });
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getEstimateDataOne() {
      let values = {}
      values['EstimateCode'] = this.cEstimateForm.EstimateCode
      getEstimateDataOne(values).then(res => {
        // this.ruleForm.ProjectName = res.data.data[0].ProjectName
        // this.ruleForm.ProjectCode = res.data.data[0].ProjectCode
        // this.ruleForm.CustomerName = res.data.data[0].CustomerName
        // this.ruleForm.EstimateCode = res.data.data[0].EstimateCode
        this.ruleForm.EstimateDate.push(res.data.data[0].EstimateStartDate)
        this.ruleForm.EstimateDate.push(res.data.data[0].EstimateEndDate)
        this.ruleForm.Work1 = res.data.data[0].Work1
        this.ruleForm.Work2 = res.data.data[0].Work2
        this.ruleForm.Work3 = res.data.data[0].Work3
        this.ruleForm.Deliverables1 = res.data.data[0].Deliverables1
        this.ruleForm.Deliverables2 = res.data.data[0].Deliverables2
        this.ruleForm.Deliverables3 = res.data.data[0].Deliverables3
        this.ruleForm.Media1 = res.data.data[0].Media1
        this.ruleForm.Media2 = res.data.data[0].Media2
        this.ruleForm.Media3 = res.data.data[0].Media3
        this.ruleForm.Quantity1 = res.data.data[0].Quantity1
        this.ruleForm.Quantity2 = res.data.data[0].Quantity2
        this.ruleForm.Quantity3 = res.data.data[0].Quantity3
        this.ruleForm.DeliveryDate1 = res.data.data[0].DeliveryDate1
        this.ruleForm.DeliveryDate2 = res.data.data[0].DeliveryDate2
        this.ruleForm.DeliveryDate3 = res.data.data[0].DeliveryDate3
        this.ruleForm.WorkSpace = res.data.data[0].WorkSpace
        this.ruleForm.Supplement = res.data.data[0].Supplement
        this.ruleForm.Remarks = res.data.data[0].Remarks
        this.ruleForm.PaymentConditions = res.data.data[0].PaymentConditions
        this.ruleForm.Other = res.data.data[0].Other
      }).catch(err => {
        console.log(err)
      })
    },
    getProductDataAll() {
      getProductDataAll().then(res => {
        this.ProductsDataAll = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    getEstimateDetailsDataAll() {
      let values = {}
      values['EstimateCode'] = this.cEstimateForm.EstimateCode
      getEstimateDetailsDataAll(values).then(res => {
        let data = res.data.data
        // 调用函数对见积详细列表数据按是否是保守进行分离
        this.separateDetailsTableData(data)
      }).catch(err => {
        console.log(err)
      })
    },
    estimatedetdatareload() {
      // 见积详细列表数据数量有变化时重新获取数据
      this.getEstimateDetailsDataAll()
    },
    separateDetailsTableData(param) {
      // 将数据以是否是保守案件(MainFlag)分离
      this.initialDetailsTableData = []
      this.runningDetailsTableData = []
      for (let data of param) {
        if (!data.MainFlag) {
          this.initialDetailsTableData.push(data)
        } else {
          this.runningDetailsTableData.push(data)
        }
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
          this.postEstimateData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    cancelForm() {
      this.$emit('cancel')
    },
    dialogTableEstimate() {
      this.detailsTableVisible = true
    },
    // resetForm(formName) {
    //   this.$refs[formName].resetFields()
    // },
    // 见积画面作业内容输入框自动检索
    querySearch(queryString, cb) {
      var restaurants = this.restaurants
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) > -1)
      }
    },
    loadAll() {
      return [
        {'value': '仕様検討'},
        {'value': '設計'},
        {'value': 'システム開発'},
        {'value': 'テスト'},
        {'value': 'HW購入'},
        {'value': 'セットアップ'},
        {'value': 'サーバー構築'},
        {'value': '導入及び保守'},
        {'value': '検証ツールご提供'},
        {'value': 'インストーラーご用意'},
        {'value': '検証ツールご提供/インストーラーご用意'},
        {'value': '仕様検討/設計'},
        {'value': 'システム開発/テスト'},
        {'value': 'HW購入/セットアップ'},
        {'value': 'サーバー構築/導入及び保守'}
      ]
    },
    handleSelect(item) {
      // 将选择的商品信息回传给data
      console.log(item)
    },
    handleChange(value) {
      console.log(value)
    }
  },
  mounted() {
    this.restaurants = this.loadAll()
  }
}
</script>
