<template>
  <div>
    <el-form :model="cEstimateDetailsFormData" :rules="rules" ref="cEstimateDetailsFormData">
      <el-row :gutter="0">
        <el-col :span="0">
          <el-form-item>
            <el-input v-model="cEstimateDetailsFormData.EstimateCode" placeholder="見積コード" :disabled="true">
              <template slot="prepend">見積コード</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="0">
          <el-form-item>
            <el-input v-model="cEstimateDetailsFormData.EstimateDetCode" placeholder="明細コード" :disabled="true">
              <template slot="prepend">明細コード</template>
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row type="flex" :gutter="0" justify="left">
        <el-col :span="10">
          <el-form-item prop="ProductName">
            <el-autocomplete
              popper-class="my-autocomplete"
              v-model="cEstimateDetailsFormData.ProductName"
              :fetch-suggestions="querySearch"
              placeholder="商品"
              style="width:100%"
              @select="handleSelect">
              <i
                class="el-icon-edit el-input__icon"
                slot="suffix"
                @click="handleIconClick">
              </i>
              <template slot-scope="{ item }">
                <div class="name">商品名：{{ item.value = item.ProductName }}</div>
              </template>
            </el-autocomplete>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item prop="Price">
            <el-input v-model="cEstimateDetailsFormData.Price" placeholder="単価">
              <!--            <template slot="prepend">単価</template>-->
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item prop="Quantity">
            <el-input v-model="cEstimateDetailsFormData.Quantity" placeholder="数量/工数">
              <!--            <template slot="prepend">数量</template>-->
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="2">
          <el-form-item prop="MainFlag">
            <el-checkbox-button v-model="cEstimateDetailsFormData.MainFlag">保 守</el-checkbox-button>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item>
            <el-button type="primary" @click="submitdetailsForm('cEstimateDetailsFormData')" plain>
              <i class="el-icon-finished"></i>追加商品
            </el-button>
          </el-form-item>
        </el-col>
        <el-col :span="0">
          <el-input v-model="SubTotal" :disabled="true">
          </el-input>
        </el-col>
        <el-col :span="0">
          <el-input v-model="Tax" :disabled="true">
          </el-input>
        </el-col>
        <el-col :span="0">
          <el-input v-model="Total" :disabled="true">
          </el-input>
        </el-col>
      </el-row>
      <!--      <el-form-item label="締切日" :label-width="formLabelWidth">-->
      <!--        <el-date-picker type="date" placeholder="締切日" v-model="detailsForm.Closing_date"-->
      <!--                        style="width: 40%;"></el-date-picker>-->
      <!--      </el-form-item>-->
    </el-form>
  </div>
</template>

<script>
import {postEstimateDetailsData} from 'api/estimates'

export default {
  name: 'EstimateDetailsForm',
  props: {
    cProductsDataAll: {
      type: Array,
      default() {
        return []
      }
    },
    cEstimateCode: {
      type: String,
      default() {
        return ''
      }
    }
  },
  data() {
    return {
      value: true,
      cEstimateDetailsFormData: {
        EstimateCode: '',
        EstimateDetCode: '',
        ProductName: '',
        ProductCode: '',
        Price: '',
        Quantity: '',
        SubTotal: '',
        Tax: '',
        Total: '',
        MainFlag: false,
        Interval: 0
        // Closing_date: '',
      },
      rules: {
        ProductName: [
          {required: true, message: '商品を選びまたは入力してください', trigger: ['change', 'blur']}
        ],
        Price: [
          {required: true, message: '単価を入力してください', trigger: ['change', 'blur']}
        ],
        Quantity: [
          {required: true, message: '必須です', trigger: 'blur'}
        ]
      }
    }
  },
  computed: {
    // 金额添加逗号
    // Price () {
    //   return this.cEstimateDetailsFormData.Price.toString().replace(/(\d)(?=(\d{3})+$)/g, '$1,')
    // },
    SubTotal() {
      return this.cEstimateDetailsFormData.Price * this.cEstimateDetailsFormData.Quantity
    },
    Tax() {
      return this.cEstimateDetailsFormData.Price * this.cEstimateDetailsFormData.Quantity * 0.1
    },
    Total() {
      return this.SubTotal + this.Tax
    }
  },
  methods: {
    newProjectcode(param) {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      var hours = date.getHours()
      var minutes = date.getMinutes()
      var seconds = date.getSeconds()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = param + year + month + strDate + hours + minutes + seconds

      return currentdate
    },
    submitdetailsForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let values = {}
          values['EstimateCode'] = this.cEstimateCode
          values['EstimateDetCode'] = this.newProjectcode('Esd')
          values['ProductCode'] = this.cEstimateDetailsFormData.ProductCode
          values['ProductName'] = this.cEstimateDetailsFormData.ProductName
          values['Quantity'] = this.cEstimateDetailsFormData.Quantity
          values['Price'] = this.cEstimateDetailsFormData.Price
          values['SubTotal'] = this.SubTotal
          values['Tax'] = this.Tax
          values['Total'] = this.Total
          values['MainFlag'] = this.cEstimateDetailsFormData.MainFlag
          postEstimateDetailsData(values).then(res => {
            console.log(res)
            let code = res.data.code
            if (code === '200') {
              // 发送数据后清空表单
              this.resetForm(formName)
              // 为防止残留之前输入商品的信息，进行表单数据的重置，当实际没有传入商品CD时，即商品列表不存在该商品时，商品CD为空
              this.cEstimateDetailsFormData.ProductCode = null
              this.cEstimateDetailsFormData.ProductName = null
              this.cEstimateDetailsFormData.Quantity = null
              this.$emit('estimatedetdatareload')
            }
          }).catch(err => {
            console.log(err)
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    querySearch(queryString, cb) {
      var cProductsDataAll = this.cProductsDataAll
      var results = queryString ? cProductsDataAll.filter(this.createFilter(queryString)) : cProductsDataAll
      // 调用 callback 返回建议列表的数据

      // console.log(this.detailsForm.Price)
      cb(results)
    },
    createFilter(queryString) {
      return (restaurant) => {
        // 从头开始匹配=== 0,全字段匹配> -1
        return (restaurant.ProductName.toLowerCase().indexOf(queryString.toLowerCase()) > -1)
      }
    },
    handleSelect(item) {
      // 将选择的商品信息回传给data
      this.cEstimateDetailsFormData.Price = item.Price
      this.cEstimateDetailsFormData.ProductCode = item.id
    },
    handleIconClick(ev) {
      console.log(ev)
    }
  }
}
</script>

